*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'Montserrat',sans-serif;
}

.banner{
  width: 100%;
  height: 100vh;
  background: url('img/temp.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  position:relative;
}

.banner_container {
  display: flex;
  align-items: center;
  height: 100vh;
}

.container{
  width: 1200px;
  max-width: 1200px;
  margin: 0 auto;
  color: #fff;
}

.banner_text {
  width: 500px;
}

.banner_text h3 {
  font-size: 50px;
}

.banner_text h1 {
  font-size: 80px;
  line-height: 80px;
  font-weight: 800;
  margin-top: 20px;
}

.banner_text p {
  font-size: 25px;
  font-weight: 400;
  margin-top: 20px;
}

.navbar {
  position: absolute;
  top:0;
  left: 0;
  width: 100%;
  
}

.navbar_text {
  display: flex;
  height: 60px;
  align-items: center;
  width: 1200px;
  margin: 0 auto;
}

.logo {
  width: 40%;
}

.logo img {
  width: 80px;
}

.navbar_ul {
  width: 60%;
  list-style-type: none;
  display: flex;
  justify-content: flex-end;
}

.navbar_ul li a {
  text-decoration: none;
  color: #fff;
  padding: 0 15px;
  font-weight: 800;
}

.banner_btn{
  margin-top: 30px;
}

.btn {
  text-decoration: none;
  border: none;
  outline: none;
  color: #fff;
  font-weight: 700;
  border-radius: 30px;
  font-size: 12px; 
  padding: 20px 40px;
}

.btn-smart {
  background: rgb(255, 69,79);
}